declare global {
    interface Window {
        ZOHO: any
    }
}

const CREATOR = window.ZOHO.CREATOR

export const get = () => {

    CREATOR.init().then((d) => {
        let config = {
            reportName: "BloodConnect_Team_Report",
            page: "1",
            pageSize: "200"
        }

        //get all records API
        CREATOR.API.getAllRecords(config).then(function (response) {
            //callback block
            console.log(response)
        });
    }).catch(e => {
        console.log(e)
    })
}

const getVolunteer = (page: number, city: string) => {
    let criteria = "(Status==\"Active\")"
    if(city !== 'All'){
        criteria = `(Status=="Active" && BloodConnect_City=="${city}")`
    }
    console.log(criteria)
    let config = {
        reportName: "BloodConnect_Team_Report",
        criteria,
        page: page.toString(),
        pageSize: "200"

    }
    return CREATOR.API.getAllRecords(config)
}

const getEvent = (page: number, city: string) => {
    let criteria = ""
    if(city !== 'All'){
        criteria = `(BloodConnect_City=="${city}")`
    }
    console.log(criteria)
    let config = {
        reportName: "Camp_Awareness_Report",
        criteria,
        page: page.toString(),
        pageSize: "200"

    }
    return CREATOR.API.getAllRecords(config)
}

const getPostCamp  = (page: number, campIDs:string[]) => {
    let criteria = "("
    campIDs.forEach(id => {
        if (criteria.length > 1)
            criteria = criteria + ` %7C%7C Camp_Awareness=${id}`
        else
            criteria = criteria + `Camp_Awareness=${id}`
    })
    console.log(criteria)
    criteria = decodeURIComponent(criteria)
    let config = {
        reportName: "Post_Camp_Form_Report",
        criteria: criteria+")",
        page: page.toString(),
        pageSize: "200"

    }
    return CREATOR.API.getAllRecords(config)
}
export const fetchVolunteer = async (city: string) => {
    await CREATOR.init()
    let page = 1
    let { data } = await getVolunteer( page++, city)
    let l = data.length
    let total = l
    while (l === 200) {
        let { data } = await getVolunteer(page++, city)
        l = data.length
        total = total + l
    }
    return total
}

export const fetchCamps = async (city: string ) => {
    let monthCampData = Array(12).fill(0)
    let monthAwarnessData = Array(12).fill(0)
    let page = 1
    let { data } = await getEvent(page++, city)
    let l = data.length
    let camps = data.filter(d => d.TypeOfEvent === 'Camp')
    let awareness = data.filter(d => d.TypeOfEvent !== 'Camp')
    camps.forEach(c => {
        let date = c.Date_field
        let [_, mon, year] = date.split('-')
        if (parseInt(year) === 2020) {
            monthCampData[parseInt(mon) - 1] = monthCampData[parseInt(mon) - 1] + 1
        }
    })
    awareness.forEach(c => {
        let date = c.Date_field
        let [_, mon, year] = date.split('-')
        if (parseInt(year) === 2020) {
            monthAwarnessData[parseInt(mon) - 1] = monthAwarnessData[parseInt(mon) - 1] + 1
        }
    })

    let campIDs = camps.map(d => d.ID)
    let totalCamps = camps.length
    let total = l
    while (l === 200) {
        let {data} = await getEvent(page++, city)
        l = data.length
        totalCamps = data.filter(d => d.TypeOfEvent === 'Camp').length
        total = total + l
        let camps = data.filter(d => d.TypeOfEvent === 'Camp')
        let awareness = data.filter(d => d.TypeOfEvent !== 'Camp')
        camps.forEach(c => {
            let date = c.Date_field
            let [_, mon, year] = date.split('-')
            if (parseInt(year) === 2020) {
                monthCampData[parseInt(mon) - 1] = monthCampData[parseInt(mon) - 1] + 1
            }
            campIDs.push(c.ID)
        })
        awareness.forEach(c => {
            let date = c.Date_field
            let [_, mon, year] = date.split('-')
            if (parseInt(year) === 2020) {
                monthAwarnessData[parseInt(mon) - 1] = monthAwarnessData[parseInt(mon) - 1] + 1
            }
        })
    }
    return {
        camps: totalCamps,
        awareness: total - totalCamps,
        campIDs,
        monthCampData,
        monthAwarnessData
    }

}


export const fetchPostCamp = async (campIDs) => {
    let monthData = Array(12).fill(0)
    let donation = 0
    let page = 1
    let response;
    try {
        response = await getPostCamp(page++, campIDs)
    } catch (e) {
        console.log('No camps')
    }
    console.log(response)
    const { code, data } = response
    if (!response || !data)
        return { totalDonation: 0, monthData }


    data.forEach(postCamp => {
        
        let date = postCamp.Camp_Awareness.display_value.split('_').pop()
        let [_, mon, year] = date.split('-')
        if (parseInt(year) === 2020) {
            monthData[parseInt(mon) - 1] = monthData[parseInt(mon) - 1] + parseInt(postCamp.Number_of_Donation)
            donation = donation + parseInt(postCamp.Number_of_Donation)
        }
    });

    return { totalDonation: donation, monthData }
}
